/* Global reset */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

ol,
ul {
  list-style: none;
}

html {
  /* font-size: 10px; */
  /* 10px / 16px = 0.625 = 62.5% */
  font-size: 62.5%;
  overflow-x: hidden;
  /* does not work on Safari */
  /* scroll-behavior: smooth; */
}

body {
  color: white;
}

/* Header */
.header {
  display: flex;
  background-color: rgb(26, 30, 31);
  justify-content: space-between;
  padding: 2rem;
  font-size: 3.2rem;
}

.header_name {
  font-size: 2.6rem;
}

@media (min-width: 900px) {
  .header {
    padding: 2rem 20rem;
  }
}

.button-nav {
  color: white;
  font-size: 1.6rem;
  border: none;
  background-color: transparent;
  cursor: pointer;
  padding: 10px 20px; /* adjust as needed */
  transition: color 0.5s ease;
}

.button-nav_active {
  color: rgb(0, 163, 224);
}

.button-nav:hover {
  color: rgb(0, 163, 224);
}
